.page {
  font-family: "Cinzel", serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  scroll-behavior: smooth;

  &__body {
    &--with-menu {
      overflow: hidden;
    }
  }
  
  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-top: 100px;

    @include onTablet {
      margin-top: 140px;
    }
  }

  &__section-title {
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -1.5px;
    text-align: center;
    color: $main-dark-color;

    @include onTablet {
      font-size: 48px;
      line-height: 48px;
    }
  }

}
